.btn{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  border-radius: 8px;
  padding-left: 10px;
  padding-right: 10px;
  &.--arrow{
    padding-left: 20px;
    svg{
      margin-left: 10px;
    }
  }

  &.--gray{
    background-color: #F4F5F7;
    color: #5946D7;
    height: 40px;
  }
  
  &.--purple{
    height: 40px;
    background-color: #5946D7;
    color: #fff;
    font-size: 16px;
    svg{
      width: 24px;
    }
  }
}

.btn__text{
  padding-top: 3px;
}