.section{
  padding-bottom: 40px;
}
.catalog-search__list{
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 25px 0;
}

.catalog-search__item{
  width: 50%;
  padding-left: 60px;
  padding-right: 60px;
  &:first-child{
    border-right: 1px solid #E8E3E3;
  }
  .search{
    margin-bottom: 10px;
  }

  .search__input{
    background-color: #fff;
  }
}

.catalog-search__name{
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 10px;
}

.catalog-search__code{
  font-size: 14px;
  line-height: 18px;
  color: #7A7680;
  span{
    color: #5946D7;
  }
}

.catalogs__wrap{
  display: flex;
  margin-right: -20px;
}

.catalogs__row{
  width: calc(25% - 20px);
  margin-right: 20px;
}

.catalogs__item {
  display: flex;
  padding: 20px 30px 20px 40px;
  box-shadow: 0px 2px 5px rgba(38, 34, 45, 0.08);
  border-radius: 10px;
  background-color: #fff;
  margin-bottom: 20px;
}

.catalogs__symbol{
  border: 1px solid #E3E3E8;
  border-radius: 10px;
  margin-right: 20px;
  font-size: 24px;
  line-height: 34px;
  font-weight: bold;
  color: #6B59CC;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  padding-top: 3px;
}

.catalog__link{
  width: 100%;
  display: block;
  font-size: 18px;
  line-height: 22px;
  color: #55556D;
  margin-bottom: 10px;
  transition: .4s;
  &:last-child{
    margin-bottom: 0px;
  }
  &:hover{
    color: #6B59CC;
  }
}


@media(max-width: 1200px){
  .catalogs__wrap{
    flex-wrap: wrap;
  }
  .catalogs__row{
    width: calc(50% - 20px);
  }
  .catalogs__row:nth-child(2){
    order: 1;
  }
  .catalogs__row:nth-child(1){
    order: -1;
  }
  .catalogs__row:nth-child(3){
    order: -1;
  }
  .catalogs__row:nth-child(4){
    order: 1;
  }
  .catalogs__item {
    margin-bottom: 10px;
    padding: 20px;
  }
  .catalog-search__item{
    padding-left: 25px;
    padding-right: 25px;
  }
  .catalog-search__name{
    font-size: 18px;
  }
}

@media(max-width: 768px){
  .catalogs__row{
    width: calc(100% - 20px);
  }
  .catalog-search__list{
    flex-wrap: wrap;
    padding: 30px 20px;
  }
  .catalog-search__item{
    padding: 0;
    width: 100%;
    &:first-child{
      padding-bottom: 30px;
      border-bottom: 1px solid #E8E3E3;
      border-right: none;
      margin-bottom: 30px;
    }
  }
}